(function e(t,n,r){function s(o,u){if(!n[o]){if(!t[o]){var a=typeof require=="function"&&require;if(!u&&a)return a(o,!0);if(i)return i(o,!0);var f=new Error("Cannot find module '"+o+"'");throw f.code="MODULE_NOT_FOUND",f}var l=n[o]={exports:{}};t[o][0].call(l.exports,function(e){var n=t[o][1][e];return s(n?n:e)},l,l.exports,e,t,n,r)}return n[o].exports}var i=typeof require=="function"&&require;for(var o=0;o<r.length;o++)s(r[o]);return s})({1:[function(require,module,exports){
$( document ).ready(function() {

	const navLogoCenter = 50;
	let currentAnchor = '';
	let mobileNavVisible = false;

	let aufzugwaerterHeight = 0;
	let leistungenHeight = 0;
	let betriebssicherheitHeight = 0;
	let downloadsHeight = 0;
	let kontaktHeight = 0;

	$('.js-show_nav').on('click',()=>{
		showMobileNav();		
	});
	$('.js-hide_nav').on('click',()=>{
		hideMobileNav();		
	});

	$('.main').on('click',()=>{
		if(mobileNavVisible){
			hideMobileNav();
		}
	});

	$('.js-goto_link').on('click', (event)=>{
		event.preventDefault();
		event.stopPropagation();
		let target = $(event.target).attr('href');
		if(target){
			target = target.replace('\/', '');			
		}
		else{
			target = '#aufzugwaerter';
		}
		currentAnchor = target;
		scrollToAnchor(target);
		positionLogoElements();
	});

	$( window ).resize(()=>{
		positionLogoElements();
		updateHeights();
	})

	let showMobileNav = ()=>{
		$('.header-wrapper').addClass('mod-visible');
		$('.header-btn.mod-nav').addClass('mod-invisible');
		$('.header-btn.mod-close').removeClass('mod-invisible');
		mobileNavVisible = true;
	}

	let hideMobileNav = ()=>{
		$('.header-wrapper').removeClass('mod-visible');
		$('.header-btn').removeClass('mod-invisible');
		$('.header-btn.mod-close').addClass('mod-invisible');
		mobileNavVisible = false;
	}

	let scrollToAnchor = (anchorId)=>{
		let element = $(anchorId);
		try{
			$('html,body').animate({scrollTop: element.offset().top},750);
			changeUrl(anchorId);
			hideMobileNav();
		}
		catch (e){
			let url = window.location.href;
			url = url.replace('datenschutz/','');
			url = url.replace('impressum/','');
			location.href = url + anchorId;
		}		
	}

	let changeUrl = (target)=>{
		if(target == '#aufzugwaerter'){
			target = ' ';
		}
		window.history.pushState({}, "", target);
	}

	let getElementTopToViewport = (element)=>{
		let top = element.offset().top - $(window).scrollTop();
		top = (-1 * $(window).height()) + top;
		top = Math.floor(top);
		return top;
	}

	let positionLogoElements = ()=>{
		let navItems = $('.list-item.mod-nav');
		let kabineTop, gewichtTop = 0;
		switch(currentAnchor){
			case '#aufzugwaerter':
				kabineTop = getElementTopToViewport(navItems.first());
				gewichtTop = getElementTopToViewport(navItems.last());
			break;
			case '#leistungen':
				kabineTop = getElementTopToViewport(navItems.eq(1));
				gewichtTop = getElementTopToViewport(navItems.eq(3));
			break;
			case '#betriebssicherheit':
				kabineTop = getElementTopToViewport(navItems.eq(2));
				gewichtTop = getElementTopToViewport(navItems.eq(2));
			break;
			case '#downloads':
				kabineTop = getElementTopToViewport(navItems.eq(3));
				gewichtTop = getElementTopToViewport(navItems.eq(1));
			break;
			case '#kontakt':
				kabineTop = getElementTopToViewport(navItems.last());
				gewichtTop = getElementTopToViewport(navItems.first());
			break;
			default:
				kabineTop = getElementTopToViewport(navItems.first());
				gewichtTop = getElementTopToViewport(navItems.last());
			break;
		}

		$('.js-top-kabine').css('top',kabineTop + navLogoCenter + 'px');
		$('.js-top-gewicht').css('top',gewichtTop + navLogoCenter + 'px');
	}

	let setCurrentUrlAnchor = ()=>{
		let anchorId = window.location.href.split('/').pop();
		if(anchorId == ''){
			anchorId = '#aufzugwaerter';
		}
		currentAnchor = anchorId;
	}

	let updateHeights = ()=>{
		aufzugwaerterHeight = $('#aufzugwaerter').innerHeight();
		leistungenHeight = $('#leistungen').innerHeight();
		betriebssicherheitHeight = $('#betriebssicherheit').innerHeight();
		downloadsHeight = $('#downloads').innerHeight();
		kontaktHeight = $('#kontakt').innerHeight();
	}

	let getAufzugwaerterHeight = ()=>{
		return aufzugwaerterHeight; 
	}
	let getLeistungenHeight = ()=>{
		return leistungenHeight; 
	}
	let getBetriebssicherheitHeight = ()=>{
		return betriebssicherheitHeight; 
	}
	let getDownloadsHeight = ()=>{
		return downloadsHeight; 
	}
	let getKontaktHeight = ()=>{
		return kontaktHeight; 
	}

	var controller = new ScrollMagic.Controller();

	new ScrollMagic.Scene({
		triggerElement: "#aufzugwaerter",
		duration: getAufzugwaerterHeight
	})
	.on('enter', function (event) {
		currentAnchor = '#aufzugwaerter';
		positionLogoElements();
	})
	.addTo(controller);

	new ScrollMagic.Scene({
		triggerElement: "#leistungen",
		duration: getLeistungenHeight
	})
	.on('enter', function (event) {
		currentAnchor = '#leistungen';
		positionLogoElements();
	})
	.addTo(controller);

	new ScrollMagic.Scene({
		triggerElement: "#betriebssicherheit",
		duration: getBetriebssicherheitHeight
	})
	.on('enter', function (event) {
		currentAnchor = '#betriebssicherheit';
		positionLogoElements();
	})
	.addTo(controller);

	new ScrollMagic.Scene({
		triggerElement: "#downloads",
		duration: getDownloadsHeight
	})
	.on('enter', function (event) {
		currentAnchor = '#downloads';
		positionLogoElements();
	})
	.addTo(controller);

	new ScrollMagic.Scene({
		triggerElement: "#kontakt",
		duration: getKontaktHeight
	})
	.on('enter', function (event) {
		currentAnchor = '#kontakt';
		positionLogoElements();
	})
	.addTo(controller);

	let initAnchor = ()=>{
		updateHeights();
		setCurrentUrlAnchor();
		positionLogoElements();
	}

	initAnchor();
});
},{}]},{},[1])

